.user-sign-form {
	max-width: 300px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	&__label {
		width: 100%;
		text-align: start;
		margin-bottom: 5px; }
	&__input {
		width: 100%;
		font-size: 20px;
		padding: 10px;
		outline: none;
		border: none;
		margin-bottom: 20px;
		&:last-of-type {
			margin-bottom: 50px; } } }
