.sign-in-window {
	background-color: rgba(0, 0, 0, 0.5);
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	&__content {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: #fff;
		min-height: 200px;
		width: 100%;
		max-width: 450px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 20px;
		text-align: center; }
	&__title {
		margin-bottom: 50px;
		font-size: 44px; }
	&__button {
		width: 100%;
		border-radius: 0;
		font-size: 25px; }
	&__header {
		display: flex;
		justify-content: flex-end;
		margin-bottom: 10px;
		svg {
			cursor: pointer;
			outline: none;
			border: none;
			transition: 0.5s;
			&:hover {
				transform: scale(2.5); } } } }
